<template lang="pug">
div
    loading(:active.sync="isLoading")
    .text-right
        button.mt-4.btn.btn-primary(type='button' @click='openModal(true)') 
        // Modal
    #addcp.modal.fade(tabindex='-1', role='dialog', aria-labelledby='exampleModalLabel', aria-hidden='true')
        .modal-dialog.modal-sm(role='document')
            .modal-content.border-0
                .modal-header.bg-dark.text-white
                    h5#exampleModalLabel.modal-title
                        span 
                    button.close(type='button', data-dismiss='modal', aria-label='Close')
                        span(aria-hidden='true') 
                .modal-body
                    .input-group.mb-3
                        .input-group-prepend
                            span#inputGroup-sizing-default.input-group-text  
                        input.form-control(v-model="cptemp.title" type='text', aria-label='Sizing example input', aria-describedby='inputGroup-sizing-default')

                    .input-group.mb-3
                        .input-group-prepend
                            span#inputGroup-sizing-default.input-group-text  
                        input.form-control(v-model="cptemp.code" type='text', aria-label='Sizing example input', aria-describedby='inputGroup-sizing-default')

                    .input-group.mb-3
                        .input-group-prepend
                            span#inputGroup-sizing-default.input-group-text  (%)
                        input.form-control(v-model="cptemp.percent" type='number', aria-label='Sizing example input', aria-describedby='inputGroup-sizing-default')

                    .input-group.mb-3
                        .input-group-prepend
                            span#inputGroup-sizing-default.input-group-text  
                        input.form-control(v-model="cptemp.due_date" type='date', aria-label='Sizing example input', aria-describedby='inputGroup-sizing-default')

                    .input-group.mb-3.text-right
                        label  
                            input(v-model="cptemp.is_enabled" type='checkbox' ,true-value="1" ,false-value="0", aria-label='Checkbox for following text input')
                            | 


                .modal-footer
                    i.fas.fa-spinner.fa-spin(v-if='status.prdLoading')
                    button.btn.btn-outline-secondary(type='button', data-dismiss='modal') 
                    button.btn.btn-primary(@click='updatecp' type='button') 
                    
    #delcp.modal.fade(tabindex='-1', role='dialog', aria-labelledby='exampleModalLabel', aria-hidden='true')
        .modal-dialog(role='document')
            .modal-content.border-0
                .modal-header.bg-danger.text-white
                    h5#exampleModalLabel.modal-title
                        span 
                    button.close(type='button', data-dismiss='modal', aria-label='Close')
                        span(aria-hidden='true') 
                .modal-body  
                    strong.text-danger  {{cptemp.title}}()
                .modal-footer
                    button.btn.btn-outline-secondary(type='button', data-dismiss='modal') 
                    button.btn.btn-danger(@click="delcp" type='button') 

    table.table.mt-4
        thead
            tr 
                th(width="120") 
            
                th 
            
                th.d-none.d-lg-table-cell.d-xl-table-cell.text-right(width="120") 
            
                th.d-none.d-lg-table-cell.d-xl-table-cell.text-right(width="120") 
            
                th(width="80") 
            
                th.text-center(width="140") 
        tbody
          template(v-for="(item,index) in cps" )
              tr(:key="item.id")
                  td {{item.title}}
                  td {{item.code}}
                  td.d-none.d-lg-table-cell.d-xl-table-cell {{item.percent}}
                  td.d-none.d-lg-table-cell.d-xl-table-cell {{item.due_date | stamp}}
                  td
                      span.text-success(v-if="item.is_enabled") 
                      span(v-else) 
                  td
                      .d-flex
                          button.btn.btn-outline-primary.btn-sm(@click="openModal(false,item)") 
                          button.btn.btn-outline-secondary.btn-sm(@click="delModal(item)") 
    pagination


</template>

<script>
import $ from "jquery";
import pagination from "./pagination";
export default {
  data() {
    return {
      cps: [],
      cptemp_date: "",
      cptemp: {},
      isNew: false,
      isLoading: false,
      status: {
        prdLoading: false
      }
    };
  },
  methods: {
    covertString(num) {
      if (!num) {
        return "";
      }
      let newDate = new Date();
      newDate.setTime(num * 1000);
      let year = newDate.getFullYear();
      // Minutes part from the timestamp
      let month = "0" + (newDate.getMonth() + 1);
      // Seconds part from the timestamp
      let day = "0" + newDate.getDate();
      return `${year}-${month.substr(-2)}-${day.substr(-2)}`;
    },
    covertStamp(time) {
      let vm = this;
      let timeNum = Date.parse(time);
      return timeNum / 1000;
    },
    getcp(page = 1) {
      //1
      const api = `${process.env.APIPATH}/api/${process.env.CUS_PATH}/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      //process.env.APIPATH  config/prod.evn.js()) or dev.env.js 
      this.$http.get(api).then(response => {
        vm.cps = response.data.coupons;
        this.$bus.$emit("getpage", response.data.pagination); //1. getprdgetpage  response.data.pagination
        vm.isLoading = false;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.cptemp = {};
        this.isNew = true;
      } else {
        this.cptemp = Object.assign({}, item); //
        this.cptemp.due_date = this.covertString(this.cptemp.due_date); // temp 
        this.isNew = false;
      }
      $("#addcp").modal("show");
    },
    delModal(item) {
      this.cptemp = Object.assign({}, item); //
      $("#delcp").modal("show");
    },
    updatecp() {
      let api = `${process.env.APIPATH}/api/${process.env.CUS_PATH}/admin/coupon`;
      let httpMethod = "post";
      const vm = this;
      vm.status.prdLoading = true;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${process.env.CUS_PATH}/admin/coupon/${vm.cptemp.id}`;
        httpMethod = "put";
      }
      vm.cptemp.due_date = vm.covertStamp(vm.cptemp.due_date); // temp 
      //process.env.APIPATH  config/prod.evn.js()) or dev.env.js 
      this.$http[httpMethod](api, { data: vm.cptemp }).then(response => {
        console.log(response.data);
        vm.status.prdLoading = false;
        if (response.data.success) {
          $("#addcp").modal("hide");
          vm.getcp();
        } else {
          console.log("", response.data);
        }
      });
    },
    delcp() {
      const vm = this;
      vm.isLoading = true;
      let api = `${process.env.APIPATH}/api/${process.env.CUS_PATH}/admin/coupon/${vm.cptemp.id}`;
      this.$http.delete(api).then(response => {
        vm.isLoading = true;
        if (response.data.success) {
          $("#delcp").modal("hide");
          vm.getcp();
        } else {
          this.$bus.$emit("message:push", response.data.message, "danger");
        }
      });
    }
  },
  components: {
    pagination
  },
  created() {
    this.getcp();
    this.$bus.$on("getprd_emit", page => {
      //vue creat getprd_emit
      this.getcp(page);
    });
  }
};
</script>





