// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
import App from './App';
import router from './router'; //
import 'bootstrap'
import { extend } from "vee-validate";
import { required, email } from "vee-validate/dist/rules"; //rulesrequired () required 

import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import './bus'; //bus
import c_filter from './filters/currency'; //currency
import stamp from './filters/stampToString'; // 

axios.defaults.withCredentials = true; //????????????cookie?

Vue.component('Loading',Loading); //
Vue.filter('c_filter',c_filter); //vue filter https://vuejs.org/v2/guide/filters.html
Vue.filter('stamp',stamp); //vue filter https://vuejs.org/v2/guide/filters.html
Vue.config.productionTip = false;
Vue.use(VueAxios,axios);
/* eslint-disable no-new */
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router: router
})
//vee-validate
extend("required", {...required,message:'{_field_} '}); // 
extend("email", {...email,message:''});


router.beforeEach((to, from, next) => {

  //meta
  if (to.meta.requiresAuth){
    const api = `${process.env.APIPATH}/api/user/check`;
    //process.env.APIPATH  config/prod.evn.js()) or dev.env.js 
    axios.post(api).then((response) => { //ajax
    if(response.data.success){
      // console.log(next)
      next(); //
    }else{
      next({path:'/login'}) //
    }
})
  }
  else{
    next()
  }

})


